.GuessWrapper{
    display: flex;
    width: 400px;
    margin:0 auto;
    border:1px solid;
    justify-content: space-around;
    position: relative;
}

.Active{
    position: absolute;
    top:0;
    border:2px solid dodgerblue;
    width:100px;
    height:70px;
    transition: left 0.5s ease;
}

.Button{
    width:150px;
    padding: 1rem;
    background-color: khaki;
    margin: 20px auto;
}

