.Node{
    width: 50px;
	height: 50px;
	border: 1px solid #000;
	border-radius: 50%;
    transition: background-color 0.5s ease;
    margin:10px 0;
}

.Node.Log{
    width: 35px;
    height: 35px;
    margin-right:5px;
}

@media (max-width: 800px){
    .Node.Log{
        width: 30px;
        height: 30px;
    }
}

@media (max-width: 750px){
    .Node.Log{
        width: 25px;
        height: 25px;
        margin:5px 5px 5px 0;
    }
}