.LogItem{
    width: 90%;
    margin: 0 auto;
    border-bottom:1px solid #c2c2c2;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 8px;
    height: 50px;
    transition: background-color 0.5s ease;
}

.LogItem:hover{
    background-color: #c2c2c2;
}

.NodeWrapper{
    display: flex;
}

.Node{
    width: 40px;
    height: 40px;
}

